


















import Vue from 'vue';
import Person from '../../data/person';

export default Vue.extend({
  name: 'WriteContext',
  props: {
    person: Person,
    updatecb: Function
  },
  data() {
    return {
      rules: [(value: string) => (!value || value.length <= 400) || 'Max 400 characters'],
      value: "" as string
    }
  },
  watch: {
    value: function(newValue: string) {
      this.updatecb(newValue || "");
    }
  }
});
