





































import Vue from 'vue';
import { AppreciationTopic } from '@/interfaces/iac';
import Person from '@/data/person';

export default Vue.extend({
  name: 'SelectTopic',
  props: {
    topics: Array as () => Array<AppreciationTopic>, 
    person: Person,
    updatecb: Function
  },
  data() {
    return {
      selected: undefined as AppreciationTopic | undefined,
      customText: "" as string,
      textRules: [
        (v: string) => !!v || 'Text is required',
        (v: string) => (v && v.length <= 40) || 'Text must be less than 40 characters',
      ]
    }
  },
  computed: {
    show(): boolean {
      return this.selected ? this.selected.id < 0 : false;
    },
    extendedTopics(): Array<AppreciationTopic> {
      return [...this.topics, this.customTopic];
    },
    customTopic(): AppreciationTopic {
      return {
        id: -1,
        text: this.customText,
        deleted: false,
        color: 'blue',
        icon: 'mdi-creation',
        subtopics: []
      } as AppreciationTopic
    }
  },
  watch: {
    selected: function(newSelection: AppreciationTopic | undefined) {
      this.updatecb(newSelection);
    },
    customTopic: function (newCustom: AppreciationTopic) {
      this.selected = newCustom;
    }
  }
});
