






























































































































import Vue from 'vue';
import SelectPerson from '@/components/create/SelectPerson.vue' 
import SelectTopic from '@/components/create/SelectTopic.vue';
import SelectSubTopic from '@/components/create/SelectSubTopic.vue';
import WriteContext from '@/components/create/WriteContext.vue';
import ReviewCard from '@/components/create/ReviewCard.vue';
import { AppreciationSubTopic, AppreciationTopic, AppreciationCardData } from '@/interfaces/iac';
import Person from '@/data/person';
import { Route } from 'vue-router';

import { peopleModule } from '@/store/modules/people';
import { acModule } from '@/store/modules/acs';
import { topicModule } from '@/store/modules/topics';

export default Vue.extend({
  name: 'Create',
  data() {
    return {
      step: 1 as number,
      person: undefined as Person | undefined,
      topic: undefined as AppreciationTopic | undefined,
      subtopics: [] as Array<AppreciationSubTopic>,
      context: "" as string,
      loading: false as boolean,
      sheet: false as boolean,
      leaving: false as boolean
    } 
  },
  components: {
    SelectPerson,
    SelectTopic,
    SelectSubTopic,
    WriteContext,
    ReviewCard
  },
  methods: {
    updatePerson(person: Person): void {
      this.person = person;
    },
    updateTopic(topic: AppreciationTopic): void {
      this.topic = topic;
    },
    updateSubTopics(subtopics: Array<AppreciationSubTopic>): void {
      this.subtopics = subtopics;
    },
    updateContext(context: string): void {
      this.context = context;
    },
    changeView(i: number): void {
      if (this.step === 1 && i === -1) this.sheet = true;
      else this.step += i;
    },
    discardAndLeave(): void {
      this.leaving = true;
      this.$router.back();
    },
    saveCard(): void {
      if (this.loading) return;
      this.loading = true;
      //TODO: handle undefined person
      if (this.person === undefined) return;
      if (this.topic === undefined) return;
      acModule.addAC({
        sender: peopleModule.selfPerson!.email,
        receiver: this.person.email,
        message: this.context,
        dateCreated: Date.now(),  
        dateSent: 0,
        state: 1,
        topic: this.topic.id >= 0 ? this.topic.id : this.topic.text,
        subtopics: this.subtopics.map(subtopic => subtopic.id >= 0 ? subtopic.id : subtopic.text)
      } as AppreciationCardData)
      .then(() => {
        this.loading = false;
        this.$router.replace({ name: 'Send' });
      });
    }
  },
  computed: {
    stepTitle(): string {
      switch (this.step) {
        case 1: return 'Select a contact'
        case 2: return 'Select a topic'
        case 3: return 'Select some subtopics'
        case 4: return 'Write reasoning'
        default: return 'Review card'
      }
    },
    topics(): Array<AppreciationTopic> {
      return topicModule.allTopics || [];
    },
    topicSubtopics(): Array<AppreciationSubTopic> {
      return this.topic ? this.topic.subtopics : [];
    },
    people(): Array<Person> {
      return peopleModule.allPeople || [];
    },
    progressValue(): number {
      return (this.step - 1) * 25;
    },
    canGoNext(): boolean {
      if (this.step === 1) return (this.person !== undefined);
      else if (this.step === 2) return this.topic !== undefined && this.topic.text.length > 0;
      else if (this.step === 3) return this.subtopics.length > 0 && this.subtopics.every(subtopic => subtopic.text.length > 0);
      else if (this.step === 4) return this.context.length > 0 && this.context.length < 500;
      else if (this.step === 5) return false;
      return true;
    },
  },
  beforeRouteLeave(to: Route, from: Route, next: CallableFunction): void {
    if (to.path === "/" && !this.leaving) {
      if (this.step > 1) this.step--;
      else this.sheet = true;
      next(false);
    } else {
      next();
    }
  }
});
