

























































import Vue from 'vue';
import { AppreciationSubTopic } from '@/interfaces/iac';
import Person from '@/data/person';

export default Vue.extend({
  name: 'SelectSubTopic',
  props: {
    subtopics: Array as () => Array<AppreciationSubTopic>, 
    person: Person,
    updatecb: Function
  },
  data() {
    return {
      toggle: false as boolean,
      selected: [] as Array<AppreciationSubTopic>,
      customTextA: "" as string,
      customTextB: "" as string,
      customTextC: "" as string,
      textRules: [
        (v: string) => !!v || 'Text is required',
        (v: string) => (v && v.length <= 40) || 'Text must be less than 40 characters',
      ]
    }
  },
  computed: {
    showA(): boolean {
      return this.selected ? this.selected.some(subtopic => subtopic.id === -1) : false;
    },
    showB(): boolean {
      return this.selected ? this.selected.some(subtopic => subtopic.id === -2) : false;
    },
    showC(): boolean {
      return this.selected ? this.selected.some(subtopic => subtopic.id === -3) : false;
    },
    extendedSubtopics(): Array<AppreciationSubTopic> {
      return [...this.subtopics, ...this.customSubTopics];
    },
    customSubTopics(): Array<AppreciationSubTopic> {
      return [
        {
          id: -1,
          text: this.customTextA,
          value: 1,
          deleted: false
        } as AppreciationSubTopic,
        {
          id: -2,
          text: this.customTextB,
          value: 1,
          deleted: false
        } as AppreciationSubTopic,
        {
          id: -3,
          text: this.customTextC,
          value: 1,
          deleted: false
        } as AppreciationSubTopic
      ]
    }
  },
  watch: {
    subtopics: function () {
      this.selected = [];
    },
    selected: function(newSelection: Array<AppreciationSubTopic>) {
      this.updatecb(newSelection);
    },
    customSubTopics: function () {
      const sel = [...this.selected.filter(subtopic => subtopic.id >= 0)];
      if (this.selected.some(subtopic => subtopic.id === -1)) 
        sel.push(this.customSubTopics.find(subtopic => subtopic.id === -1)!);
      if (this.selected.some(subtopic => subtopic.id === -2)) 
        sel.push(this.customSubTopics.find(subtopic => subtopic.id === -2)!);
      if (this.selected.some(subtopic => subtopic.id === -3)) 
        sel.push(this.customSubTopics.find(subtopic => subtopic.id === -3)!);
      this.selected = sel;
    }
  }
});
