


































































import Vue from 'vue';
import Person from '@/data/person';
import NewPerson from '@/data/newperson';

export default Vue.extend({
  name: 'SelectPerson',
  props: {
    people: Array as () => Array<Person>, 
    updatecb: Function
  },
  data() {
    return {
      selected: undefined as Person | undefined
    }
  },
  methods: {
    imageSource(person: Person): string {
      return person.image ? require(`@/assets/${person.image.path}`) : '';
    }
  },
  computed: {
    show(): boolean {
      return this.selected ? this.selected.isNewContact : false;
    },
    sortedPeople(): Array<Person> {
      const copy = [...this.people];
      return [...copy.sort((a, b) => {
        if (a.name < b.name) return -1;
        if (a.name > b.name) return 1;
        return 0;
      }), new NewPerson()];
    },
  },
  watch: {
    selected: function(newSelection: Person | undefined) {
      this.updatecb(newSelection);
    }
  }
});
