









































































import Vue from 'vue';
import AppreciationCard from '@/data/ac';
import Person from '@/data/person';
import { peopleModule } from '@/store/modules/people';
import { AppreciationSubTopic, AppreciationTopic } from '../../interfaces/iac';
import iconConfig from '@/store/misc/icons';

export default Vue.extend({
  name: 'ReviewCard',
  props: {
    person: Object as () => Person,
    topic: Object as () => AppreciationTopic,
    subtopics: Array as () => Array<AppreciationSubTopic>,
    context: String ,
  },
  data() { 
    return {
      swap: true,
      iconConfig
    }
  },
  computed: {
    imageSource(): string {
      return this.person.image ? require(`@/assets/${this.person.image.path}`) : '';
    },
    
  }
});
